import React, { useState, useContext } from 'react'


const AppContext=React.createContext();

const AppProvider=({ children }) => {
    const [isSiderbarOpen, setIsSidebareOpen]=useState(false)
    const [isMODALOpen, setIsmODALOpen]=useState(false)

    const openSiderBar=() => {
        setIsSidebareOpen(true);
    }
    const closeSiderBar=() => {
        setIsSidebareOpen(false);
    }
    const openModal=() => {
        setIsmODALOpen(true);
    }
    const closeModal=() => {
        setIsmODALOpen(false);
    }

    return <AppContext.Provider value={{ isMODALOpen, isSiderbarOpen, openModal, openSiderBar, closeModal, closeSiderBar }}>
        {children}
    </AppContext.Provider>
}

//custom hook
export const useGlobalContext=() => {
    return useContext(AppContext);
}

export { AppContext, AppProvider }